aside
	float: left
	width: 24%
	min-width: 340px
	margin-right: 1%
	+clr
.sidebox
	float: left
	width: 100%
	-webkit-border-radius: 5px
	border-radius: 5px
	padding: 40px 40px 50px
	background-color: #FFF
	+clr



.tab__box
	a
		display: block
		font-size: 16px
		color: #151515
		margin-left: 40px
		margin-bottom: 20px
		+osl
		+tr(.3s)
		&:hover
			color: $purpur
		&.is_active
			color: $purpur