.select {
  position: relative;
  width: 100%;
  cursor: pointer;
  font-size: 15px;
  font-weight: bold;
  z-index: 10;
}

.select__text {
  position: relative;
  display: block;
  height: 34px;
  line-height: 34px;
  padding: 0 20px;
  font-size: 16px;
  border: 1px solid #e8ecef;
  background: #fff;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  box-sizing: border-box;
  font-family: "Open Sans", Arial, sans-serif;
  font-weight: 300;
}
.select__text:before {
  content: "";
  position: absolute;
  top: 15px;
  right: 10px;
  background-image: url("../img/icons.png");
  width: 12px;
  height: 7px;
  background-position: -165px -74px;
}

.select__list {
  position: absolute;
  display: none;
  width: 100%;
  left: 1px;
  right: 1px;
  top: 34px;
  z-index: 100;
  background: #fff;
}

.select__list li {
  width: 100%;
  font-size: 15px;
  line-height: 30px;
  padding: 0 20px;
  cursor: pointer;
  -webkit-transition: background 0.2s linear;
  -moz-transition: background 0.2s linear;
  -o-transition: background 0.2s linear;
  transition: background 0.2s linear;
  font-family: "Open Sans", Arial, sans-serif;
  font-weight: 300;
}
