.radio {
  display: inline-block;
  color: #8a93a3;
  position: relative;
  cursor: pointer;
}
.radio input {
  position: absolute;
  left: 0;
  top: 4px;
  opacity: 0;
  z-index: 2;
}
.radio input:checked + span:before {
  background-color: #52abf0;
}
.radio input:checked + span:after {
  content: "";
  position: absolute;
  left: -33px;
  top: 8px;
  z-index: 12;
  background-image: url("../img/icons.png");
  width: 11px;
  height: 9px;
  background-position: -166px -86px;
}
.radio span {
  position: relative;
  display: inline-block;
  padding-left: 0px;
  margin: 0 40px 5px 40px;
  font-size: 16px;
  color: #23252a;
  font-family: "Open Sans", Arial, sans-serif;
  font-weight: 300;
}
.radio span:before {
  content: "";
  position: absolute;
  left: -40px;
  top: 0;
  background-color: #dfe5e7;
  width: 24px;
  height: 24px;
  z-index: 10;
}

