.page {
  display: inline-block;
  font-size: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  *zoom: 1;
}
.page:after {
  content: " ";
  display: table;
  clear: both;
}
.page li {
  float: left;
  margin-right: -1px;
  line-height: 45px;
  height: 45px;
  border: 1px solid #d8dde2;
  -webkit-border-radius: 5px;
  border-radius: 5px;
}
.page a {
  font-size: 18px;
  color: #23252a;
  display: block;
  padding: 0 15px;
  -moz-transition: 0.3s;
  -o-transition: 0.3s;
  -webkit-transition: 0.3s;
  -ms-transition: 0.3s;
  transition: 0.3s;
}
.page a:hover, .page a.is_active {
  color: #eb6c96;
}

